.projects-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

.projects-container button {
  margin-top: 10px;
  padding: 8px 12px;
  background-color: #112240;
  color: white;
  border: none;
  cursor: pointer;
  border-radius: 5px;
}
.projects-container button:hover {
  background-color: #1d3557;
}
